"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { useState } from "react";

interface FingerprintData {
  userAgent: string;
  webrtc: string;
  timezone: string;
  location: string;
  language: string[] | string;
  displayLanguage: string;
  screenResolution: string;
  fonts: string;
  canvas: string;
  webglImage: string;
  audioContext: string;
  mediaDevices: string;
  clientRects: string;
  speechVoices: string;
  webglMetadata: {
    renderer: string;
    vendor: string;
  };
  webgpu: string;
  cpu: string;
  ram: string;
  deviceName: string;
  macAddress: string;
  doNotTrack: string;
  portScanProtection: string;
  hardwareAcceleration: string;
  disableTLSFeatures: string;
  hardwareVendor: string;
  hardwareModel: string;
}

interface FingerprintCardProps {
  fingerprint?: FingerprintData;
  fingerprints?: FingerprintData[];
  bulk?: boolean;
  metadata?: {
    mode: string;
    generatedAt: string;
    count?: number;
    options?: any;
  };
}

export function FingerprintCard({ fingerprint, fingerprints, bulk, metadata }: FingerprintCardProps) {
  const [copiedField, setCopiedField] = useState<string | null>(null);
  const [selectedIndex, setSelectedIndex] = useState(0);

  const copyToClipboard = async (text: string, fieldName: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopiedField(fieldName);
      setTimeout(() => setCopiedField(null), 2000);
    } catch (err) {
      console.error('Failed to copy:', err);
    }
  };

  const downloadFingerprint = (data: any, type: string) => {
    let content: string;
    let filename: string;
    let mimeType: string;

    if (type === 'single') {
      content = JSON.stringify(data, null, 2);
      filename = `fingerprint_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.json`;
      mimeType = 'application/json';
    } else if (type === 'csv') {
      content = convertToCSV([data]);
      filename = `fingerprint_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.csv`;
      mimeType = 'text/csv';
    } else if (type === 'bulk-json') {
      content = JSON.stringify(data, null, 2);
      filename = `fingerprints_bulk_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.json`;
      mimeType = 'application/json';
    } else if (type === 'bulk-csv') {
      content = convertToCSV(data);
      filename = `fingerprints_bulk_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.csv`;
      mimeType = 'text/csv';
    } else {
      return;
    }

    const blob = new Blob([content], { type: mimeType });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const convertToCSV = (data: any[]) => {
    if (!data || data.length === 0) return '';
    
    const headers = Object.keys(data[0]);
    const csvHeaders = headers.join(',');
    
    const csvRows = data.map(item => {
      return headers.map(header => {
        const value = item[header];
        if (typeof value === 'object') {
          return `"${JSON.stringify(value).replace(/"/g, '""')}"`;
        }
        return `"${String(value).replace(/"/g, '""')}"`;
      }).join(',');
    });
    
    return [csvHeaders, ...csvRows].join('\n');
  };

  const formatLanguage = (language: string[] | string) => {
    if (Array.isArray(language)) {
      return language.join(', ');
    }
    return language;
  };

  const formatJsonField = (field: string) => {
    try {
      const parsed = JSON.parse(field);
      return JSON.stringify(parsed, null, 2);
    } catch {
      return field;
    }
  };

  const currentFingerprint = bulk && fingerprints ? fingerprints[selectedIndex] : fingerprint;

  if (!currentFingerprint) {
    return null;
  }

  const basicFields = [
    { key: 'userAgent', label: 'User Agent', copyable: true },
    { key: 'webrtc', label: 'WebRTC' },
    { key: 'timezone', label: 'Timezone' },
    { key: 'location', label: 'Location' },
    { key: 'language', label: 'Language', formatter: formatLanguage },
    { key: 'displayLanguage', label: 'Display Language' },
    { key: 'screenResolution', label: 'Screen Resolution' },
  ];

  const systemFields = [
    { key: 'cpu', label: 'CPU' },
    { key: 'ram', label: 'RAM' },
    { key: 'deviceName', label: 'Device Name' },
    { key: 'macAddress', label: 'MAC Address', copyable: true },
    { key: 'hardwareVendor', label: 'Hardware Vendor' },
    { key: 'hardwareModel', label: 'Hardware Model' },
  ];

  const privacyFields = [
    { key: 'doNotTrack', label: 'Do Not Track' },
    { key: 'portScanProtection', label: 'Port Scan Protection' },
    { key: 'hardwareAcceleration', label: 'Hardware Acceleration' },
    { key: 'disableTLSFeatures', label: 'Disable TLS Features' },
  ];

  const fingerprintFields = [
    { key: 'fonts', label: 'Fonts' },
    { key: 'canvas', label: 'Canvas Hash' },
    { key: 'webglImage', label: 'WebGL Image' },
    { key: 'audioContext', label: 'Audio Context' },
    { key: 'mediaDevices', label: 'Media Devices', formatter: formatJsonField },
    { key: 'clientRects', label: 'Client Rects' },
    { key: 'speechVoices', label: 'Speech Voices', formatter: formatJsonField },
    { key: 'webgpu', label: 'WebGPU' },
  ];

  const FieldRow = ({ field, value, copyable = false, formatter }: { 
    field: string; 
    value: any; 
    copyable?: boolean;
    formatter?: (val: any) => string;
  }) => {
    const displayValue = formatter ? formatter(value) : String(value);
    const copyValue = typeof value === 'object' ? JSON.stringify(value) : String(value);
    
    return (
      <div className="flex justify-between items-start py-3 border-b border-gray-100 last:border-b-0 hover:bg-gray-50 transition-colors">
        <div className="font-medium text-gray-700 min-w-0 flex-1 pr-4">
          {field}
        </div>
        <div className="flex items-center gap-2 min-w-0 flex-1">
          <div className="text-gray-900 break-all text-sm whitespace-pre-wrap">
            {displayValue}
          </div>
          {copyable && (
            <Button
              variant="outline"
              size="sm"
              onClick={() => copyToClipboard(copyValue, field)}
              className="shrink-0 h-7 px-3 text-xs hover:bg-blue-50"
            >
              {copiedField === field ? '✓ Copied!' : '📋 Copy'}
            </Button>
          )}
        </div>
      </div>
    );
  };

  return (
    <Card className="w-full max-w-5xl mx-auto shadow-lg">
      <CardHeader className="bg-gradient-to-r from-blue-50 to-purple-50">
        <div className="flex justify-between items-start">
          <div>
            <CardTitle className="text-3xl font-bold text-gray-900 mb-2">
              {bulk ? '🔄 Generated Browser Fingerprints (Bulk)' : '🔍 Generated Browser Fingerprint'}
            </CardTitle>
            {metadata && (
              <div className="flex gap-2 mt-3 flex-wrap">
                <Badge variant="secondary" className="px-3 py-1">
                  {metadata.mode === 'random' ? '🎲 Random Generation' : '⚙️ Manual Configuration'}
                </Badge>
                <Badge variant="outline" className="px-3 py-1">
                  🕒 {new Date(metadata.generatedAt).toLocaleString()}
                </Badge>
                {bulk && metadata.count && (
                  <Badge variant="default" className="px-3 py-1 bg-blue-600">
                    📊 {metadata.count} Fingerprints
                  </Badge>
                )}
              </div>
            )}
          </div>
        </div>
      </CardHeader>
      
      <CardContent className="space-y-8 p-6">
        {/* Bulk Navigation */}
        {bulk && fingerprints && fingerprints.length > 1 && (
          <div className="flex items-center justify-between p-4 bg-gradient-to-r from-blue-50 to-indigo-50 rounded-lg border border-blue-200">
            <div className="flex items-center gap-3">
              <span className="text-sm font-semibold text-blue-800">📋 Fingerprint:</span>
              <select 
                value={selectedIndex} 
                onChange={(e) => setSelectedIndex(Number(e.target.value))}
                className="px-4 py-2 border border-blue-300 rounded-lg text-sm bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              >
                {fingerprints.map((_, index) => (
                  <option key={index} value={index}>
                    #{index + 1}
                  </option>
                ))}
              </select>
              <span className="text-sm text-blue-600 font-medium">of {fingerprints.length}</span>
            </div>
            <div className="flex gap-2">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setSelectedIndex(Math.max(0, selectedIndex - 1))}
                disabled={selectedIndex === 0}
                className="border-blue-300 text-blue-700 hover:bg-blue-50"
              >
                ← Previous
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setSelectedIndex(Math.min(fingerprints.length - 1, selectedIndex + 1))}
                disabled={selectedIndex === fingerprints.length - 1}
                className="border-blue-300 text-blue-700 hover:bg-blue-50"
              >
                Next →
              </Button>
            </div>
          </div>
        )}

        {/* Basic Information */}
        <div className="bg-white rounded-lg border border-gray-200">
          <h3 className="text-xl font-bold mb-4 text-gray-800 bg-gray-50 px-4 py-3 rounded-t-lg border-b">
            📋 Basic Information
          </h3>
          <div className="px-4 pb-4">
            {basicFields.map(field => (
              <FieldRow
                key={field.key}
                field={field.label}
                value={currentFingerprint[field.key as keyof FingerprintData]}
                copyable={field.copyable}
                formatter={field.formatter}
              />
            ))}
          </div>
        </div>

        {/* System Information */}
        <div className="bg-white rounded-lg border border-gray-200">
          <h3 className="text-xl font-bold mb-4 text-gray-800 bg-gray-50 px-4 py-3 rounded-t-lg border-b">
            💻 System Information
          </h3>
          <div className="px-4 pb-4">
            {systemFields.map(field => (
              <FieldRow
                key={field.key}
                field={field.label}
                value={currentFingerprint[field.key as keyof FingerprintData]}
                copyable={field.copyable}
              />
            ))}
          </div>
        </div>

        {/* WebGL Information */}
        <div className="bg-white rounded-lg border border-gray-200">
          <h3 className="text-xl font-bold mb-4 text-gray-800 bg-gray-50 px-4 py-3 rounded-t-lg border-b">
            🎮 WebGL Information
          </h3>
          <div className="px-4 pb-4">
            <FieldRow
              field="WebGL Renderer"
              value={currentFingerprint.webglMetadata.renderer}
            />
            <FieldRow
              field="WebGL Vendor"
              value={currentFingerprint.webglMetadata.vendor}
            />
          </div>
        </div>

        {/* Privacy Settings */}
        <div className="bg-white rounded-lg border border-gray-200">
          <h3 className="text-xl font-bold mb-4 text-gray-800 bg-gray-50 px-4 py-3 rounded-t-lg border-b">
            🔒 Privacy Settings
          </h3>
          <div className="px-4 pb-4">
            {privacyFields.map(field => (
              <FieldRow
                key={field.key}
                field={field.label}
                value={currentFingerprint[field.key as keyof FingerprintData]}
              />
            ))}
          </div>
        </div>

        {/* Advanced Fingerprint Data - Collapsible */}
        <Accordion type="single" collapsible className="w-full">
          <AccordionItem value="fingerprint-data" className="border border-gray-200 rounded-lg">
            <AccordionTrigger className="text-xl font-bold text-gray-800 bg-gray-50 px-4 py-3 rounded-t-lg hover:bg-gray-100">
              🔬 Advanced Fingerprint Data
            </AccordionTrigger>
            <AccordionContent>
              <div className="px-4 pb-4 pt-2">
                {fingerprintFields.map(field => (
                  <FieldRow
                    key={field.key}
                    field={field.label}
                    value={currentFingerprint[field.key as keyof FingerprintData]}
                    formatter={field.formatter}
                  />
                ))}
              </div>
            </AccordionContent>
          </AccordionItem>
        </Accordion>

        {/* Export Options */}
        <div className="bg-gradient-to-r from-gray-50 to-blue-50 rounded-lg border border-gray-200 p-6">
          <h4 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
            📤 Export Options
          </h4>
          
          {/* Copy Buttons */}
          <div className="mb-6">
            <h5 className="text-sm font-semibold text-gray-700 mb-3">📋 Copy to Clipboard</h5>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
              <Button
                variant="outline"
                onClick={() => copyToClipboard(JSON.stringify(currentFingerprint, null, 2), 'full-json')}
                className="w-full h-11 border-blue-300 hover:bg-blue-50"
              >
                {copiedField === 'full-json' ? '✅ Copied JSON!' : '📋 Copy JSON'}
              </Button>
              <Button
                variant="outline"
                onClick={() => copyToClipboard(currentFingerprint.userAgent, 'user-agent-only')}
                className="w-full h-11 border-green-300 hover:bg-green-50"
              >
                {copiedField === 'user-agent-only' ? '✅ Copied User-Agent!' : '📋 Copy User-Agent'}
              </Button>
              {bulk && fingerprints && (
                <Button
                  variant="outline"
                  onClick={() => copyToClipboard(JSON.stringify(fingerprints, null, 2), 'bulk-json')}
                  className="w-full h-11 md:col-span-2 border-purple-300 hover:bg-purple-50"
                >
                  {copiedField === 'bulk-json' ? '✅ Copied All!' : '📋 Copy All JSON'}
                </Button>
              )}
            </div>
          </div>

          {/* Download Buttons */}
          <div>
            <h5 className="text-sm font-semibold text-gray-700 mb-3">💾 Download Files</h5>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
              <Button
                variant="default"
                onClick={() => downloadFingerprint(currentFingerprint, 'single')}
                className="w-full h-11 bg-blue-600 hover:bg-blue-700 text-white"
              >
                💾 Download JSON
              </Button>
              <Button
                variant="default"
                onClick={() => downloadFingerprint(currentFingerprint, 'csv')}
                className="w-full h-11 bg-green-600 hover:bg-green-700 text-white"
              >
                📊 Download CSV
              </Button>
              {bulk && fingerprints && (
                <>
                  <Button
                    variant="default"
                    onClick={() => downloadFingerprint(fingerprints, 'bulk-json')}
                    className="w-full h-11 bg-purple-600 hover:bg-purple-700 text-white"
                  >
                    💾 Download All JSON
                  </Button>
                  <Button
                    variant="default"
                    onClick={() => downloadFingerprint(fingerprints, 'bulk-csv')}
                    className="w-full h-11 bg-orange-600 hover:bg-orange-700 text-white"
                  >
                    📊 Download All CSV
                  </Button>
                </>
              )}
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
